* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.number {
  width: 74.4px;
  height: 75px;
}
#calculator {
  margin: auto;
  border: 1px solid black;
  width: 300px;
  height: 430px;
  position: relative;
}
#equals {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 100px;
  width: 75px;
  z-index: 10;
}
#numbers {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 225px;
  height: 300px;
}
#zero {
  width: 149px;
}
#add {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: 100px;
  height: 100px;
  width: 75px;
}
#subtract {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: 200px;
  height: 100px;
  width: 75px;
}
#clear {
  position: absolute;
  left: 0;
  bottom: 0;
  margin-bottom: 300px;
  height: 55px;
  width: 149px;
}
#multiply {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: 300px;
  height: 55px;
  width: 75px;
}
#divide {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: 300px;
  margin-right: 75px;
  height: 55px;
  width: 75px;
}
#display {
  position: absolute;
  right: 0;
  top: 0;
  margin-top: 40px;
  height: 40px;
  width: 100%;
  text-align: right;
  padding-right: 5px;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#formula {
  position: absolute;
  right: 0;
  top: 0;
  height: 40px;
  width: 100%;
  text-align: right;
  padding-right: 5px;
  overflow: hidden;
  white-space: nowrap;
  padding-top: 5px;
  border-bottom: 1px solid black;
}
footer {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: -15px;
  font-size: 10px;
}
